.table {
  width: 100%; }
  .table td {
    transition: 0.3s; }
  .table .td-short {
    width: 50px; }
  .table .red {
    background-color: #ff000012; }
  .table .blue {
    background-color: #0000ff17; }
  .table input {
    background: none;
    border: none; }
  .table button {
    border: none;
    background: none;
    border-right: 2px solid black; }
  .table button:hover {
    cursor: pointer; }
